import { nullPass } from '../../../components/filter/index.js';
export default {
	data() {
		return {
			colorInfo: {no: '', color: ''},
			id: '',
		}
	},
	onLoad(option) {
		this.id = option.id;
		if (nullPass(this.id)) {
			this.getInfo();
		} else {
			this.$API.generateNo().then(res => {
				this.colorInfo.no = res;
			});
		}
	},
	methods: {
		getInfo () { // 根据id查询颜色
			this.$API.get('/fuxi/color/query-color', {colorId: this.id}).then(res => {
				if (res.code === 'success') {
					uni.showToast({
						title: '保存成功!'
					});
					this.cleaData();
				}
			});
		},
		save () { // 保存
			let url = '';
			if (!nullPass(this.colorInfo.color)) {
				uni.showToast({
					title: '颜色名称不能为空!',
					icon: 'none'
				});
				return;
			}
			if (nullPass(this.id)) {
				url = '/fuxi/color/update-color';
			} else {
				url = '/fuxi/color/add-color';
			}
			this.$API.post(url, this.colorInfo).then(res => {
				if (res.code === 'success') {
					uni.showToast({
						title: '保存成功!'
					});
					if (nullPass(this.id)) {
						this.$API.bank();
					} else {
						this.$API.generateNo().then(res => {
								this.colorInfo.no = res;
							});
							this.colorInfo.color = '';
						}
					}
					
			});
		}
	}
	
}
